.intro {
  display: flex;

  height: calc(100vh - 60px);
  background-color: #171d20;
  margin-top: 60px;
  .intro-text {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .intro-heading {
    font-size: 3rem;
    margin: 0 0 3% 20%;
  }
  .intro-subheading {
    font-size: 2.5rem;
    margin: 0 0 0 40%;
  }

  .intro-visual {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;

    img {
      width: 500px;
    }
  }
  .hilight {
    color: #76b041;
    opacity: 1;
  }
}

@media (max-width: 1100px) {
  .intro {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .intro-text {
      width: 80%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
    .intro-heading {
      font-size: 5rem;
      margin: 0;
    }
    .intro-subheading {
      font-size: 3.5rem;
      margin: 1% 0 3% 0;
    }
    .intro-visual {
      display: flex;
      align-items: center;
      justify-content: center;
      img {
        width: 500px;
        margin-left: 5%;
      }
    }
  }
}
@media (orientation: landscape) and (max-width: 900px) {
  .intro {
    display: flex;
    flex-direction: row;
    height: calc(100vh - 60px);
    background-color: #171d20;
    margin-top: 60px;
    justify-content: space-around;

    .intro-text {
      padding-left: 40px;
      width: 55%;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
    }
    .intro-heading {
      font-size: 3rem;
      margin: 0 0 3% 0%;
    }
    .intro-subheading {
      font-size: 2.5rem;
      margin: 0 0 0 0%;
    }

    .intro-visual {
      width: 40%;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      margin-bottom: 20px;

      img {
        padding-right: 60px;
        width: 250px;
      }
    }
    .hilight {
      color: #76b041;
      opacity: 1;
    }
  }
}
@media (max-width: 480px) {
  .intro {
    display: flex;

    min-height: 90vh;
    height: 100%;
    background-color: #171d20;
    margin-top: 60px;
    .intro-text {
      display: flex;
      padding: 7% 0;
      width: 100%;
    }
    .intro-heading {
      font-size: 2rem;
      margin: 0 0 1% 0;
    }
    .intro-subheading {
      font-size: 1.7rem;
      margin: 0 0 0 0;
    }

    .intro-visual {
      margin-top: 2%;
      img {
        width: 250px;
      }
    }
    .hilight {
      color: #76b041;
      opacity: 1;
    }
  }
}
